@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background: #f7f7f8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  scroll-behavior: smooth;
}

.intro{
  margin: 10px 0 0 0;
  padding: 0 20px 0 20px;
  height: 100vh;
}

.cust_h1{
    font-size: 1.8em;
    font-weight: 700;
    text-align: justify;
}

.cust_h3{
  font-size: 0.7em;
  font-weight: 400;
  font-style: italic;
  font-feature-settings: normal;
  font-variant: normal;
  text-align: center;
  color: 	#007c87;
}

@media only screen and (min-width: 768px) {
  .intro { 
    height: 100vh;
  }

  .cust_h1{
    font-size: 5em;
    font-weight: 700;
    text-align: justify;
  }

  .cust_h3{
    font-size: 20px;
    font-weight: 400;
    font-style: italic;
    font-feature-settings: normal;
    font-variant: normal;
    text-align: center;
    color: 	#007c87;
  }
}

@media only screen and (min-width: 992px) {
  .intro {
    padding: 200px 200px 200px 200px;
    height: 100vh;
  }

  .cust_h1{
    font-size: 7em;
    font-weight: 700;
    text-align: justify;
  }

  .cust_h3{
    font-size: 30px;
    font-weight: 400;
    font-style: italic;
    font-feature-settings: normal;
    font-variant: normal;
    text-align: center;
    color: 	#007c87;
  }
}


.section_style{
    padding: 25px 0 0 0;
}

.about_text{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    text-align: justify;
}

.tool{
    font-weight: 200;
    
}

.tool_text{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    text-align: justify;
}

.icon{
    height: 75px;
    width: 75px;
}

.icon_credit{
    font-size: 0.5em;
}
.cust_card{
    margin: 10px 2px 10px 2px;
    height: auto;
    max-height: auto;
    width: 450px;
}

.project-intro{
    margin: 100px 0 0 0;
}

.project_icon{
    height: 35px;
    width: 50px;
}

.cust_card_text{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    text-align: justify;
}

.card-img-top{
    height: 200px;
    overflow: hidden;
}

.card-text{
    text-align:  justify;
}

.cust_card_heading{
    color: black;
}

.my-col{
    border: 1px solid red;
}
